import { Injectable } from '@angular/core';
import { Client } from '../models/client.model';
import { HttpClient, HttpErrorResponse, HttpHeaders }  from '@angular/common/http';
import { Observable } from 'rxjs/internal/Observable';

@Injectable()
export class clientService {
    constructor (private httpClient: HttpClient) {}
    private clientList: Client[] = [
        {
            User_Name: 'alex1987',
            Full_Name: 'Alex',
            userID: 1,
            Date_of_birth: new Date('06/08/1987'),
            Gender: 'Male',
            Email: 'alex@alex.com',
            Password: 1234,
            pic: 'assets/clientpic.png',
            isActive: true,
            PhotoPath: null,
            previewPhoto: false
        },
        {
            User_Name: 'AlmaQueen',
            Full_Name: 'Alma',
            userID: 2,
            Date_of_birth: new Date('01/01/2016'),
            Gender: 'Female',
            Email: 'Alma@malinois.com',
            Password: 4321,
            pic: 'assets/spaceDog.jpg',
            isActive: true,
            PhotoPath: null,
            previewPhoto: false,
        },
        {
            User_Name: 'First-Man',
            Full_Name: 'Neil Armstrong',
            userID: 3,
            Date_of_birth: new Date('08/05/1930'),
            Gender: 'Male',
            Email: 'FirstMan@nasa.com',
            Password: 210769,
            pic: 'assets/Neil_Armstrong.jpg',
            isActive: false,
            PhotoPath: null,
            previewPhoto: false,
        },
    ];

    getClient(): Observable<Client[]>{
        return this.httpClient.get<Client[]>('http://localhost:52366/api/users');
    }

    private handleError (errorResponse: HttpErrorResponse) {
        if(errorResponse.error instanceof ErrorEvent) {
            console.error('Client Side Error: ', errorResponse.error.message);
        }
        else { 
            console.error('Server Side Error: ', errorResponse);
        }
    }

    save(client: Client): Observable<Client[]> {
        if (client.userID === 3) {
            return this.httpClient.post<Client[]>('http://localhost:52366/api/users', client, {
                headers: new HttpHeaders ({
                    'Content-Type': 'application/json'
                })
            })
        } 
        else {
            const foundIndex = this.clientList.findIndex(e => e.userID === client.userID);
            this.clientList[foundIndex] = client;
        }
        
    }
}
