import { Injectable } from '@angular/core';
import { Rents } from '../models/rents.model';
import { HttpClient, HttpErrorResponse, HttpHeaders }  from '@angular/common/http'; 
import { Observable } from 'rxjs/internal/Observable';

@Injectable()
export class RentService {
    constructor (private httpClient: HttpClient) {}
    private rentList: Rents[] = [
    {
        Car_ID: 1,
        Model: 'Mars Rover',
        User_ID: 2,
        Daily_Cost: 500, 
        Start_date: new Date('06/08/1987'),
        Number_of_days: 5,
        Transmision: 'Auto',
    },
    {
        Car_ID: 2,
        Model: 'Lunar  Rover',
        User_ID: 3,
        Daily_Cost: 235, 
        Start_date: new Date('02/09/1990'),
        Number_of_days: 10,
        Transmision: 'Manual'
    },
    {
        Car_ID: 4,
        Model: 'Jamaica Rover',
        User_ID: 2,
        Daily_Cost: 100, 
        Start_date: new Date('02/02/2020'),
        Number_of_days: 2,
        Transmision: 'Manual'
    },
    ];

    getRents(): Observable<Rents[]> {
       return this.httpClient.get<Rents[]>('http://localhost:52366/api/rent');
    }

    save(rents: Rents){
        if (rents.Car_ID === null) {
            return this.httpClient.post<Rents[]>('http://localhost:52366/api/rent', rents, {
            headers: new HttpHeaders ({
                'Content-Type': 'application/json'
            })
        });
       } 
       else {
            const foundIndex = this.rentList.findIndex(e => e.Car_ID === rents.Car_ID);
            this.rentList[foundIndex] = rents;
        }
    
    }

    deleteRent(Car_ID: number): Observable<void> {
        return this.httpClient.delete<void>('http://localhost:52366/api/rent');
    }
}
    
