import { PipeTransform, Pipe } from '@angular/core';
import { Client } from 'src/app/models/client.model';
import { pipe } from 'rxjs';

@Pipe({
    name: 'clientFilter'
})
export class clientFilterPipe implements PipeTransform {
    transform(client: Client[], serchTerm: string): Client[] {
        if (!client || !serchTerm){
            return client;
        }

        return client.filter(client => client.Full_Name.toLowerCase().indexOf(serchTerm.toLowerCase()) !== -1)
    }
}